import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, LoadingController } from 'ionic-angular';
import { Http } from '@angular/http';
import { AppConfig } from '../../../app/app.config';
import 'rxjs/add/operator/map';
/**
 * Generated class for the TaskmorePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-taskmore',
  templateUrl: 'taskmore.html',
})
export class TaskmorePage {
  url = AppConfig.getUrl() + "/task/finish?userId="+localStorage.userid;
  mess:any;

  constructor(public navCtrl: NavController, public navParams: NavParams, public http: Http, public alertCtrl: AlertController, public loadingCtrl: LoadingController) {
    this.mess = this.navParams.data.mess;
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad TaskmorePage');
  }

  goto() {
    let loading = this.loadingCtrl.create({
      content: '加载中...'
    });
    loading.present();
    return new Promise((resolve, reject) => {
      this.http.get(this.url + "&taskId=" + this.mess.taskid)
        .map(res => res.json())
        .subscribe(data => {
          resolve(data);
          if (data.status) {
            let alert = this.alertCtrl.create({
              title: "操作成功"
            });
            alert.present();
            this.mess.finishTime = new Date();
          } else {
            let alert = this.alertCtrl.create({
              title: data.msg
            });
            alert.present();
          }
        }, err => {
          let alert = this.alertCtrl.create({
            title: err
          });
          alert.present();
        })
      loading.dismiss();
    })
  }
}



